<script>
import announcement from '@/views/Announcement.vue'
import $ from 'jquery'
export default {
  name: 'Navbar',
  data () {
    return {
      cart: {},
      scrollY: 0
    }
  },
  mounted () {
    window.addEventListener('scroll', this.onScroll)
  },
  components: {
    announcement
  },
  created () {
    this.getCart()
  },
  methods: {
    getCart (loader) {
      const url = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_UUID}/ec/shopping`
      this.$http.get(url)
        .then((res) => {
          this.cart = res.data.data
          if (loader) {
            loader.hide()
          }
        })
    },
    collapseToggle (id) {
      $(`#${id}`).collapse('toggle')
    },
    onScroll (e) {
      this.scrollY = window.top.scrollY
    }
  }
}
</script>
